<template>
  <f7-app :params="f7Params">
    <f7-statusbar></f7-statusbar>
    <f7-panel left reveal>
      <f7-view url="/panel-left/" links-view=".view-main"></f7-view>
    </f7-panel>
    <f7-panel right reveal>
      <f7-view url="/panel-right/"></f7-view>
    </f7-panel>
    <f7-view url="/" :main="true" class="safe-areas" :master-detail-breakpoint="800"></f7-view>
  </f7-app>
</template>
<script>
import { f7App, f7Panel, f7View, f7Statusbar } from 'framework7-vue'
import routes from './routes'

export default {
  components: {
    f7App,
    f7Panel,
    f7View,
    f7Statusbar
  },
  data () {
    // Demo Theme
    let theme = 'auto'
    if (document.location.search.indexOf('theme=') >= 0) {
      theme = document.location.search.split('theme=')[1].split('&')[0]
    }
    return {
      f7Params: {
        theme,
        routes,
        id: 'com.zihai.app',
        name: '影子'
      }
    }
  },
  created () {
    let uname = localStorage.getItem('username')
    let pword = localStorage.getItem('password')
    if (!(uname != null && pword != null)) { return false }
    var url = process.env.API_HOST + 'shiro/login.do'
    this.$f7.request.promise({
      method: 'POST',
      contentType: 'application/json',
      url: url,
      crossDomain: true,
      xhrFields: {
        withCredentials: true
      },
      data: {username: uname, password: pword}
    }).then(function (data) {
      if (data === 'OK') {
        console.log('auto login enter')
      }
    })
  }
}
</script>



