<template>
  <f7-page>
    <f7-navbar title="我的状态" back-link="Back"></f7-navbar>
    <f7-list inline-labels>
      <f7-list-input
        label="昵称" type="text" clearbutton
        :value="info.alias" @input="info.alias = $event.target.value"
      />
      <f7-list-input
        label="关注的人" type="text" clearbutton
        :value="info.doing" @input="info.doing = $event.target.value"
      />
      <f7-list-input
        label="背景音乐" type="text" clearbutton
        :value="info.music" @input="info.music = $event.target.value"
      />
      <f7-list-input
        label="关注的书" type="text" clearbutton
        :value="info.book" @input="info.book = $event.target.value"
      />
      <f7-list-input
        label="关注的运动" type="text" clearbutton
        :value="info.sport" @input="info.sport = $event.target.value"
      />
      <f7-list-input
        label="喜欢的" type="text" clearbutton placeholder="请输入喜欢相关的关键词（如：A,B）,它将用于邀请索引"
        :value="info.like" @input="info.like = $event.target.value"
      />
      <f7-list-input
        label="经常打卡的地方" type="text" clearbutton placeholder="请输入一个位置，系统将自动为您打卡"
        :value="info.place" @input="info.place = $event.target.value"
      />
    </f7-list>
    <f7-list>
      <f7-list-button @click="saveInfo">保存</f7-list-button>
    </f7-list>

  </f7-page>
</template>

<script>
import {f7Navbar, f7Page, f7BlockTitle, f7List, f7ListInput, f7ListButton} from 'framework7-vue'
import {evil} from '../assets/js/util.js'
export default {
  created: function () {
    const self = this
    self.getInfo()
  },
  components: {
    f7Navbar,
    f7Page,
    f7BlockTitle,
    f7List,
    f7ListInput,
    f7ListButton
  },
  data: function () {
    return {
      info: {
        alias: '',
        doing: '',
        like: '',
        music: '',
        book: '',
        sport: '',
        place: ''
      }
    }
  },
  methods: {
    getInfo () {
      const self = this
      var url = process.env.API_HOST + 'user/userInfo.do'
      this.$f7.request.promise({
        method: 'GET',
        url: url,
        crossDomain: true,
        xhrFields: {
          withCredentials: true
        }
      }).then(function (data) {
        let data2 = evil(data)
        if (data2.success === true) {
          self.info = data2.data
        } else {
          self.toastbuttom(data2.message)
        }
      })
    },
    saveInfo () {
      const self = this
      const router = self.$f7router
      var url = process.env.API_HOST + 'user/userInfo.do'
      this.$f7.request.promise({
        method: 'POST',
        contentType: 'application/json',
        url: url,
        crossDomain: true,
        xhrFields: {
          withCredentials: true
        },
        data: self.info
      }).then(function (data) {
        if (data === 'OK') {
          self.toastbuttom('保存成功')
          router.back()
        } else {
          self.toastbuttom(data)
        }
      })
    },
    toastbuttom (data) {
      const self = this
      if (!self.toastBottom) {
        self.toastBottom = self.$f7.toast.create({
          text: data,
          cssClass: 'sd_center',
          closeTimeout: 2000
        })
      }
      // Open it
      self.toastBottom.open()
    }
  }
}
</script>

<style scoped>

</style>



